@import './../common';

@mixin theme($theme-name, $primary-darker, $primary-lighter, $secondary, $light) {
  &.#{$theme-name} {
    background-color: $light;
    color: $primary-darker;

    .title {
      background-color: $primary-lighter;
      color: $light;
    }

    .publication {
      text-decoration-color: $secondary;
    }

    .read-online a {
      background-color: $secondary;
      color: $primary-darker;
    }
  }
}

.lit-card {
  border-radius: 2px;
  width: 100%;
  height: auto;
  padding: 20px;

  .title,
  .categories {
    text-align: center;
  }

  .title {
    margin-top: 0;
    padding: 5px;
  }

  .categories {
    font-size: small;
    font-style: italic;
  }

  .details {
    display: grid;
    grid-template-columns: 1fr 40%;
    gap: 10px;
    padding: 20px 0;

    .with-image {
      height: 100%;
      min-height: 150px;
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
      cursor: zoom-in;
      transform-origin: right center;
      transition: transform 0.5s;

      &:hover {
        transform: scale(2);
      }
    }
  }

  .publication {
    text-decoration: underline;
  }

  .read-online {
    word-wrap: break-word;

    a {
      padding: 3px;
      text-decoration: none;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  .good-reads-icon {
    display: flex;
    justify-content: center;
    border: 1px solid #553B08;
    border-radius: 50%;
    width: 30px;
    height: 30px;
    background-color: #FFF;
    color: #553B08;
    font-size: 24px;
    font-family: 'Josefin Sans', sans-serif;
  }

  @include theme(reply, $primary-reply-darker, $primary-reply-lighter, $secondary-reply, $light-reply);
  @include theme(basil, $primary-basil-darker, $primary-basil-lighter, $secondary-basil, $light-basil);

  &.basil .read-online a {
    color: $light-basil;
  }
}

@media only screen and (min-width: 600px) {
  .lit-card .details .with-image {
    transform-origin: center;

    &:hover {
      transform: scale(2.75);
    }
  }
}