@import './../common';

@mixin theme($theme-name, $primary-darker, $secondary, $light) {
  &.#{$theme-name} {
    background-color: $light;
    color: $primary-darker;

    &.capital-letter>p:first-child::first-letter,
    a {
      color: $secondary;
    }
  }
}

.description {
  padding: 10px;

  &.capital-letter>p:first-child::first-letter {
    float: left;
    padding-top: 5px;
    padding-right: 5px;
    font-size: 70px;
    line-height: 40px;
  }

  a {
    text-decoration: none;

    &:hover {
      text-decoration: underline;
    }
  }

  @include theme(reply, $primary-reply-darker, $secondary-reply, $light-reply);
  @include theme(basil, $primary-basil-darker, $secondary-basil, $light-basil);

  &.basil {

    &.capital-letter>p:first-child::first-letter,
    a {
      color: $secondary-basil;
    }
  }
}