.card-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, max-content));
  gap: 30px;
  justify-content: center;
  margin-top: 30px;
}

@media only screen and (min-width: 768px) {
  .card-container {
    grid-template-columns: repeat(auto-fill, minmax(350px, max-content));
  }
}